@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url(/aprozar-la-garaj/static/media/home.657bfcef.jpeg);
  background-position: center;
  font-family: 'Comfortaa', cursive;
}
.App{
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-y: scroll;
}

.App-header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 7vh;
}

header h1{
  font-size: 10vw;
  font-family: 'Pacifico', cursive;
  color:white;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
  /* src: url('Pacifico-Regular.ttf') format('truetype'); */
}

h1{
  font-size: 1vw;
  font-family: 'Pacifico', cursive;
  color:gray;
  line-height: 0;
}

h2 {
  font-family: 'Comfortaa', cursive;
  font-feature-settings: normal;
  font-variant-caps: normal;
  font-size: 3vw;
  padding: 1em;
  background-color: black;
  color: yellow;
  z-index: -1;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
}

.main {
  background-color: white;
  min-height: 100vh;
  padding-top:10vh;
  clear: both;
}

.head{
  width: 100%;
  margin:0;
}

.navigation { 
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white;
  z-index: 9;
  clear: both;
}

.bradius {
  border-radius: 25px;
}

.w75 {
  width: 75vw;
}

.left-100 {
  left: 100%;
}

.left-0 {
  left: 0;
}

#menu {
  transition: .2s;
}

.grow:hover {
  z-index: 2;
  transition: .3s;
}

.grow {
  z-index: 0;
  transition: .3s;
}
